import axios from 'axios';

import React, { Component } from 'react';

import endpoints from '../../redux/api/endpoints';
import { replaceItemInDictArrayById, toggleArrayDictById, lodashToggle } from '../../components/utils';
import { parseErrorData } from '../../redux/api/utils';
import { ShiftToSave, CreatedShift, EmployeesBlock } from '../../components/ShiftCreateComponent';


export class ShiftCreateComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lumbers: [],
      initLumbers: [],
      totalVolume: 0,
      totaCash: 0,

      createdShift: null,

      employees: [],
      activeEmployees: [],

      dataToSave: null,

      message: null,
      error: null,
    }
    this.calcRow = this.calcRow.bind(this);
    this.calcRowAndTotal = this.calcRowAndTotal.bind(this);
    this.calcTotalVolume = this.calcTotalVolume.bind(this)
    this.calcTotalCash = this.calcTotalCash.bind(this)
    this.addEmployee = this.addEmployee.bind(this)
    this.saveData = this.saveData.bind(this)
    this.preSave = this.preSave.bind(this)
    this.back = this.back.bind(this)
  }

  componentDidMount() {
    axios.get(endpoints.RAMSHIK_SHIFT_CREATE_DATA)
      .then(res => {
        const initData = res.data;
        this.setState({ lumbers: initData.lumbers, initLumbers: initData.lumbers, employees: initData.employees });
      })
  }

  calcRow (lumber, qnty, moreThan10) {
    let rate = lumber.employee_rate
    if (moreThan10 && lumber.employee_rate == 600) rate = 650
    if (!moreThan10 && lumber.employee_rate == 650) rate = 600
    if (!moreThan10 && lumber.employee_rate == 600) rate = lumber.employee_rate

    let calcLumber = {
      ...lumber,
      lumber: lumber.id,
      quantity: qnty,
      volume_total: qnty * lumber.volume,
      cash: qnty * lumber.volume * rate,
      employee_rate: rate
    }
    return calcLumber
  }

  calcTotalVolume (lumbers) {
    let totalVolumeVar = 0

    lumbers.map(lumber => {
      totalVolumeVar = totalVolumeVar + lumber.volume_total
    })

    return totalVolumeVar
  }

  calcTotalCash (lumbers) {
    let totalCashVar = 0

    lumbers.map(lumber => {
      totalCashVar = totalCashVar + lumber.cash
    })

    return totalCashVar
  }

  calcRowAndTotal (e, lumber) {
    let { lumbers } = this.state
    let calcLumber = this.calcRow(lumber, e.target.value, false)
    lumbers = replaceItemInDictArrayById(lumbers, calcLumber)

    let totalVolume = this.calcTotalVolume(lumbers)
    if (totalVolume >= 10) {
      lumbers.map((lmbr, idx) => {
        lumbers[idx] = this.calcRow(lmbr, lmbr.quantity, true)
      })
      totalVolume = this.calcTotalVolume(lumbers)
    } else {
      lumbers.map((lmbr, idx) => {
        lumbers[idx] = this.calcRow(lmbr, lmbr.quantity, false)
      })
      totalVolume = this.calcTotalVolume(lumbers)
    }

    let totalCash = this.calcTotalCash(lumbers)
    
    this.setState({ 
        ...this.state,
        lumbers: lumbers,
        totalVolume: totalVolume,
        totalCash: totalCash,
        message: null
        })
  }

  addEmployee (employee) {
    let { activeEmployees } = this.state
    activeEmployees = toggleArrayDictById(activeEmployees, employee)
    this.setState({
      ...this.state,
      activeEmployees: activeEmployees
    })
  }

  preSave () {
    const { lumbers, totalCash, totalVolume, activeEmployees } = this.state
    let eIds = []
    activeEmployees.map(emp => eIds = lodashToggle(eIds, emp.id))
    let raw_records = []
    lumbers.map(lumber =>{
      if (lumber.cash > 0 && lumber.quantity > 0) {
        raw_records = lodashToggle(raw_records, lumber)
      }
    })
    let data = {
      shift_type: 'day',
      date: null,
      raw_records: raw_records,
      employees: eIds,
      employee_cash: totalCash,
      volume: totalVolume,
      employeesData: activeEmployees,
      cash_per_employee: totalCash / eIds.length
    }

    this.setState({
      ...this.state,
      dataToSave: data,
    })
  }

  saveData () {
    const { dataToSave} = this.state
    const token = localStorage.getItem('token');
    axios({
      method: 'post',
      url: endpoints.RAMSHIK_SHIFT_CREATE,
      data: dataToSave,
      headers: { 'content-type': 'application/JSON', 'Authorization': `JWT ${token}` }
    })
    .then(response => {
      this.setState({ message: ' .', createdShift: response.data });
    })
    .catch(err => {
        // const error = new Error(err);
        // error.data = parseErrorData(err);
        this.setState({ message: '' });
        // throw error;
    })

    this.setState({
      ...this.state,
      activeEmployees: [],
      lumbers: this.state.initLumbers,
      totalVolume: 0,
      totalCash: 0,
      note: '',
      dataToSave: null,
    })
  }

  back () {
    this.setState({dataToSave: null})
  }

  render() {
    const { lumbers, totalVolume, totalCash, employees, activeEmployees, message, createdShift, dataToSave} 
      = this.state
    return (
      <div>
        {createdShift
          ? <CreatedShift shift={createdShift} message={message}/>
          : dataToSave
            ? <ShiftToSave shift={dataToSave} saveData={this.saveData} back={this.back}/>
            : <div>
                <div className='card card-style mb-1 mt-2'>
                  <div className='content'>
                    <h2></h2>
                    {lumbers.length > 0 &&
                      <table className='table table-sm table-responsive text-center' 
                        style={{'lineHeight': '17px', 'color': '#6c6c6c'}}>
                        <thead>
                          <th></th>
                          <th>-</th>
                          <th></th>
                          <th></th>
                          <th></th>
                        </thead>
                        <tbody>
                        {lumbers.map((lumber, key) => 
                          <tr key={key}>
                            <td>{lumber.name} {lumber.wood_species}</td>
                            <td className='w-25'>
                              <input style={{'color': '#6c6c6c'}}
                              type='number' className='w-75' onChange={(e) => this.calcRowAndTotal(e, lumber)} 
                              value={lumber.quantity > 0 && lumber.quantity}/>
                            </td>
                            <td>{lumber.volume_total > 0 && lumber.volume_total.toFixed(4) + ' 3'}</td>
                            <td>{lumber.employee_rate} </td>
                            <td>{lumber.cash > 0 && lumber.cash.toFixed(0) + ' '}</td>
                          </tr>
                          )}
                          <tr>
                            <td></td>
                            <td className='w-25'>
                              -
                            </td>
                            <td>
                              <span className='font-17 font-600'>
                                {totalVolume > 0 && totalVolume.toFixed(4) + ' 3'}
                              </span>
                            </td>
                            <td>-</td>
                            <td>
                              <span className='font-17 font-600'>
                                {totalCash > 0 && totalCash.toFixed(0) + ' '}
                              </span>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    }
                  </div>
                </div>
                <div className='card card-style mb-3'>
                  <EmployeesBlock 
                    totalCash={totalCash} 
                    employees={employees} 
                    activeEmployees={activeEmployees} 
                    addEmployee={this.addEmployee}/>

                </div>
                {(activeEmployees.length > 0 && totalCash > 0) &&
                  <button   
                      onClick={this.preSave}
                      className='btn btn-center-xl btn-xxl text-uppercase font-900 bg-highlight rounded-sm shadow-l'>
                      
                    </button>
                }
              </div>          
          }
      </div>
    )
  }
}