import React, { Component } from 'react';


export function LumberTable (props) {
  const { lumbers } = props
  return(
    <table className='table table-sm table-responsive mt-2'>
      <thead>
        <th>
          
        </th>
        <th>
          -
        </th>
        <th>
          
        </th>
        <th>
          
        </th>
        <th>
          
        </th>
      </thead>
      <tbody>
        {lumbers.map(lumber_record =>
        <tr>
          <td>{lumber_record.lumber}</td>
          <td>{lumber_record.quantity} </td>
          <td>{lumber_record.volume} 3</td>
          <td>{lumber_record.rate || lumber_record.employee_rate} </td>
          <td>{(lumber_record.total_cash || lumber_record.cash).toFixed(0)} </td>
        </tr>
          )}
      </tbody>
    </table>
  )
}


export function ShiftToSave (props) {
  const { shift, saveData, back } = props
  const rowClass = 'mb-0 font-15 font-500 color-black'
  return (
    <div className='card card-style my-2'>
      <div className='content'>
      <h4> </h4>
      {shift &&
          <div className=''>
            <p className={rowClass}>
               : {shift.volume.toFixed(4)} 3</p>
            <p className={rowClass}>
                 : {shift.employee_cash.toFixed(1)} </p>
            <p className={rowClass}>
                 : {shift.cash_per_employee.toFixed(1)} </p>
            <p className={rowClass}>: {shift.note}</p>

            <LumberTable lumbers={shift.raw_records}/>

            {shift.employeesData && shift.employeesData.length > 0 &&
              <div>
                <h5> ?</h5>
                {shift.employeesData.map(emp => 
                  <p className='mb-0'>{emp.nickname}</p>
                  )}
              </div>
            }
          </div>
        }
        <div className='d-flex justify-content-around'>
          <button onClick={back}
            className='btn btn-m text-uppercase font-900 bg-dark color-white rounded-sm shadow-l'>
            
          </button>
          <button onClick={saveData}
            className='btn btn-m text-uppercase font-900 bg-highlight rounded-sm shadow-l'>
               
          </button>
        </div>
      </div>
    </div>
  )
}


export function CreatedShift (props) {
  const { shift, message } = props
  const rowClass = 'mb-0 font-15 font-500 color-black'
  return (
    <div className='card card-style my-2'>
      <div className='content'>
        <h4> </h4>
        {shift &&
          <div className=''>
            <p className={rowClass}>: {shift.date}</p>
            <p className={rowClass}>
               : {shift.volume.toFixed(2)}  ({shift.back_calc_volume.toFixed(2)}) 3</p>
            <p className={rowClass}>
                 : {shift.employee_cash.toFixed(1)} ({shift.back_calc_cash.toFixed(1)}) 
              </p>
            <p className={rowClass}>
                 : {shift.cash_per_employee.toFixed(1)}
              ({shift.back_calc_cash_per_employee.toFixed(1)}) </p>
            <p className={rowClass}>: {shift.note}</p>

            <LumberTable lumbers={shift.lumber_records}/>
            {shift.employees && shift.employees.length > 0 &&
              <div>
                <h5> ?</h5>
                {shift.employees.map(emp => 
                  <p className='mb-0'>{emp}</p>
                  )}
              </div>
            }
          </div>
        }
        {message &&
          <h2 className='color-green1-light text-center'> !</h2> 
        }
      </div>
    </div>
  )
}

export function EmployeesBlock (props) {
  const { totalCash, employees, activeEmployees, addEmployee } = props
  const empClass = ' rounded-xs px-2 py-2 '
  return (
    <div className='content'>
      <h4> ? {activeEmployees.length > 0 && '(' + activeEmployees.length + ')'}</h4>
      <div className='d-flex justify-content-between my-2'>
        {employees.map(emp => 
          <div 
            className={activeEmployees.includes(emp) ? 'bg-green2-light' + empClass : 'bg-dark1-dark' + empClass }
             onClick={() => addEmployee(emp)}>
            {emp.nickname}
          </div>
          )}
      </div>
      {activeEmployees.length > 0 && 
        <div>
          <div className='d-flex justify-content-start'>
            {activeEmployees.map(aEmp => 
              <div className='mx-2'>
                <span className='d-block font-16'>{aEmp.nickname}</span>
                {totalCash > 0 && 
                  <span className='font-17 font-600'> {(totalCash / activeEmployees.length).toFixed(0)} </span>
                }
              </div>
              )}
          </div>
        </div>
      }
    </div>
  )
}